import React from "react";
import {
  MDBRow,
  MDBCol,
  MDBCard,
  MDBCardBody,
  MDBCardTitle,
  MDBCardText,
  MDBCardImage,
} from "mdb-react-ui-kit";
import { Link } from "react-router-dom";
import { excerpt } from "../utility";

const RelatedTours = ({ relatedTours, tourId }) => {
    const filteredTours = relatedTours.filter((item) => item._id !== tourId);
    const slicedTours = filteredTours.slice(0, 3);

    return (
      <>
        {slicedTours.length > 0 && (
          <>
            {slicedTours.length > 1}
            <h4>Related Tours</h4>
            <MDBRow className="row-col-1 row-cols-md-3 g-4">
              {slicedTours.map((item) => (
                <MDBCol key={item._id}>
                  <MDBCard>
                    <Link to={`/tour/${item._id}`}>
                      <MDBCardImage src={item.imageFile} alt={item.title} position="top" />
                    </Link>
                    <span className="text-start tag-card">
                      {item.tags.map((tag) => (
                        <Link to={`/tours/tag/${tag}`} >
                          #{tag}
                        </Link>
                      ))}
                    </span>
                    <MDBCardBody>
                      <MDBCardTitle className='text-start' >{item.title}</MDBCardTitle>
                     <MDBCardText className ='text-start'> {excerpt(item.description,45)}</MDBCardText>
                </MDBCardBody>
                  </MDBCard>
                </MDBCol>
              ))}
            </MDBRow>
          </>
        )}
      </>
    );
  };

